import {defineStore} from "pinia/dist/pinia";
import {ref} from "vue";
import {ACCOUNT_STORAGE_KEY} from "@/constants";

export const personStore = defineStore('store', () => {
    const savedPerson = localStorage.getItem(ACCOUNT_STORAGE_KEY);
    let person = ref(savedPerson ? JSON.parse(savedPerson) : '')

    function toggleFavorite(id) {
        let image = person.value.textToImages.images.find((image) => image.id === id);
        image.like = !image.like;
    }

    function deleteImage(id) {
        let idImage = person.value.textToImages.images.findIndex((image) => image.id === id);
        person.value.textToImages.images.splice(idImage, 1);
    }

    return {person, toggleFavorite, deleteImage}
})
